import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);

export default new Vuex.Store({
  plugins: [
    createPersistedState({
      // getState: (key) =>Cookies.getJSON(key),
      // setState: (key, state) => Cookies.set(key, state, {expires: 3, secure: true})
      storage: window.sessionStrorage,
    }),
  ],

  state: {
    authenticated: false,
    user: null,
    isAdmin: true,
    project_selected: 0,

  },
  getters: {
    authenticated(state) {
      return state.authenticated;
    },
    user(state) {
      return state.user;
    },
    isAdmin(state) {
      return state.isAdmin;
    },   
    project_selected(state) {
      return state.project_selected;
    },
  },
  mutations: {
    setUser(state, payload) {
      state.user = payload;
    },
    setAdmin(state, payload) {
      state.isAdmin = payload;
    },
    setauthenticated(state, payload) {
      state.authenticated = payload;
    },
    setproject_selected(state, payload) {
      state.project_selected = payload;
    },
  },
  actions: {
    async selectedProject({ commit }, payload) {
      commit("setproject_selected", payload);
    },

    async signOut({ commit }) {
      commit("setUser", null);
      commit("setauthenticated", false);
      window.sessionStrorage.clear();
    },

    async signIn({ commit }, payload) {
      try {
        await axios.get("/sanctum/csrf-cookie");

        const res = await axios.post("/api/authenticate", payload);
        if (res.data.status_code != 200) {
          throw res.data.message;
        }
        //

        axios
          .get("/api/user")
          .then((res) => {
            commit("setUser", res.data);
            commit("setauthenticated", true);
          })
          .catch(() => {
            commit("setUser", null);
            commit("setauthenticated", false);
          });
      } catch (e) {
        throw "User cannot be authenticated";
      }
    },
  },
  async checkAuthenticated({ commit }) {
    try {
      await axios.get("/sanctum/csrf-cookie");
      const res = await axios.get("/api/authenticate");
      if (res.data.status_code != 200) {
        throw res.data.message;
      }
      if (res.data.status_code === 200) {
        commit("setUser", res.data);
        commit("setauthenticated", true);
      } else {
        commit("setUser", null);
        commit("setauthenticated", false);
      }
    } catch (e) {
      throw "User cannot be checked if authenticated ";
    }
  },
});
